import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { ToastController, LoadingController, Events } from '@ionic/angular';
import { UsuariosService } from '../services/usuarios.service';
@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
/**
 * Tela inicial responsável por autenticar o usuário
 * @author Carlos W. Gama (carloswgama@gmail.com)
 */
export class LoginPage implements OnInit {

  public form: FormGroup;

  constructor(private router:Router, public formBuilder: FormBuilder,
             public toastController: ToastController, private usuariosService:UsuariosService, private loadingController:LoadingController, private events:Events) { }

  ngOnInit() {
    this.form = this.formBuilder.group({
      codigo:[null, Validators.required],
      senha: [null, [Validators.required, Validators.minLength(5)]]
    })

  }
  /** Loga o usuario no Sistema */
  async logar() {
    let dados = this.form.value;
    let loading = await this.loadingController.create({message:'Aguarde', backdropDismiss:false });
    loading.present();
    let resposta = await this.usuariosService.logar(dados.codigo, dados.senha);
    if (resposta.sucesso) {
      this.events.publish('recriar');
      loading.dismiss();
      this.router.navigateByUrl((resposta.coordenador ? '/coordenador' : '/paciente')) //Redireciona 
    } else {
      //Falha no login
      loading.dismiss();
      this.toastController.create({
        message: 'Código ou Senha incorreta',
        duration: 3000,
        translucent: true
      }).then(t => t.present());
    }
  }

}
